	module.exports = {"pathContext":{"docHtml":"<h1>1.1.0 / 2017-10-17</h1>\n<ul>\n<li>\n<p>Add option to add waiters to the front of the queue</p>\n<h1>1.0.0 / 2017-06-29</h1>\n</li>\n<li>\n<p>Remove dependency on Q by using the global Promise variable by default. Thank you @erikvold (<a href=\"https://github.com/erikvold\">https://github.com/erikvold</a>) for the PR</p>\n<h1>0.3.10 / 2017-06-27</h1>\n</li>\n<li>\n<p>Remove dependencies on Q-specific nodify(), try(), and defer() methods so that you can inject e.g. standard ES6 promises using <code>new AsyncLock({ Promise: Promise })</code></p>\n<h1>0.3.9 / 2016-11-30</h1>\n</li>\n<li>\n<p>Referred to MIT license in package.json</p>\n</li>\n<li>\n<p>Update NPM module dependencies, remove no-longer-maintained blanket code coverage</p>\n</li>\n<li>\n<p>Change author to rogierschouten</p>\n</li>\n<li>\n<p>Fix invalid JSDoc comments (thanks @JonathanPicques)</p>\n</li>\n<li>\n<p>Bugfix: TypeError when locking multiple keys at once</p>\n<h1>0.3.7-0.3.8</h1>\n<p>Unknown, previous author did not commit this to Github.</p>\n<h1>0.3.6 / 2015-09-07</h1>\n</li>\n<li>\n<p>Performance improvement</p>\n<h1>0.3.5 / 2015-06-15</h1>\n</li>\n<li>\n<p>Performance improvement</p>\n<h1>0.3.4 / 2015-06-09</h1>\n</li>\n<li>\n<p>Bug fix</p>\n<h1>0.3.3 / 2015-05-19</h1>\n</li>\n<li>\n<p>Bug fix</p>\n<h1>0.3.2 / 2015-05-08</h1>\n</li>\n<li>\n<p>Set default timeout to never</p>\n<h1>0.3.1 / 2015-04-15</h1>\n</li>\n<li>\n<p>Use your own promise</p>\n<h1>0.3.0 / 2015-03-06</h1>\n</li>\n<li>\n<p>Domain reentrant</p>\n<h1>0.2.0 / 2015-02-21</h1>\n</li>\n<li>\n<p>Support promise mode</p>\n</li>\n<li>\n<p>Pending task limit</p>\n<h1>0.1.0 / 2015-01-13</h1>\n</li>\n<li>\n<p>Initial version</p>\n</li>\n</ul>","docPath":"argo-ci/node_modules/async-lock/history","proj":"argo-ci"}}

